import AppState from '../app';
import { Logger } from '../../utils/logger';
import { nextId } from 'jsmodules/lib/system/id';

/**
 * Ê®°ÂùóÂÖ•Âè£Ê†áËÆ∞
 * @param target ÂÆû‰æãÂØπË±°
 * @param name ÂáΩÊï∞Âêç
 * @param descriptor ÂáΩÊï∞ÂÆû‰æã
 */
export function module(target:IAppModule, name, descriptor){
    let method  = descriptor.value;
    target.name =`module_${nextId()}`;
    //Ê≥®ÊÑè,ËøôÈáå‰∏çË¶ÅÁî®ÁÆ≠Â§¥ÂáΩÊï∞,Â∑≤‰øùÊåÅthisÂÖ≥ÈîÆÂ≠óÊòØÂéüÊù•ÁöÑÂØπË±° ‚ö†Ô∏è
    descriptor.value = function(...args){
        var self=this;
        const exec=async ()=>{
            let result =await method.apply(self,args);
            self.__ready__=true;
            return result
        }
        return exec();
    }
    //Â¶ÇÊûúÊ®°ÂùóÊ≤°Êúâ‰∏Ä‰∏™Âè´installÁöÑÂáΩÊï∞,Â∞±Âä†‰∏Ä‰∏™
    if(name!="install"){
        target.install=async()=>{
            return descriptor.value();
        }
    }
    target.__installed__=true;
}

/**
 * Ê®°ÂùóÊö¥Èú≤ÁªôÂ§ñÈÉ®Ë∞ÉÁî®ÁöÑÈ™åËØÅÂáΩÊï∞
 * @param target ÂÆû‰æãÂØπË±°
 * @param name ÂáΩÊï∞Âêç
 * @param descriptor ÂáΩÊï∞ÂÆû‰æã
 */
export function moduleFunc(target:IAppModule,name,descriptor){
    let method  = descriptor.value;
    //Ê≥®ÊÑè,ËøôÈáå‰∏çË¶ÅÁî®ÁÆ≠Â§¥ÂáΩÊï∞,Â∑≤‰øùÊåÅthisÂÖ≥ÈîÆÂ≠óÊòØÂéüÊù•ÁöÑÂØπË±° ‚ö†Ô∏è
    descriptor.value = function(...args){
        var self=this;
        if(!self.__installed__){
            throw new Error(`Ê®°Âùó${self.name}Êú™ÂÆâË£Ö`);
        }
        if(!self.__ready__){
            throw new Error(`Ê®°Âùó${self.name}‰∏çÂèØÁî®`);
        }
        return method.apply(self,args);
    }
}

/**
 * ÁÆÄÈôãÁöÑÊ®°Âùó üòç
 */
export interface IAppModule{
    /**
     * Ê®°ÂùóÂèãÂ•ΩÂêçÁß∞
     */
    name?:string;
    /**
     * Ê®°ÂùóÊòØÂê¶ÂèØÁî®
     */
    __ready__?:boolean;
    /**
     * Ê®°ÂùóÊòØÂê¶ÂÆâË£Ö
     */
    __installed__?:boolean;

    /**
     * ÂÆâË£ÖÊ®°Âùó
     */
    install?:()=>any;
}